/* eslint-disable */
import Vue from 'vue';
import Router from 'vue-router';
const Index = () => import ('@/components/Index');
const CivilSMS = () => import ('@/components/CivilSMS');
const OuterSMS = () => import ('@/components/OuterSMS');
const EmailPush = () => import ('@/components/EmailPush');
const SMSReply = () => import ('@/components/SMSReply');
Vue.use (Router);

export default new Router ({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
    },
    {
      path: '/civil-sms',
      name: 'civilSMS',
      component: CivilSMS,
    },
    {
      path: '/outer-sms',
      name: 'outerSMS',
      key: 'OuterSMS',
      component: OuterSMS,
    },
    {
      path: '/email-push',
      name: 'emailPush',
      key: 'EmailPush',
      component: EmailPush,
    },
    {
      path: '/sms-reply',
      name: 'smsReply',
      key: 'SMSReply',
      component: SMSReply,
    },
    {
      path: '*',
      redirect: '/404',
    },
  ],
});



