<template>
  <a-layout id="components-layout-demo-responsive" style="min-height: 100%;">
    <a-layout-header class="header" :style="{ background: '#fff', padding: 0 }">
      <a href="/">
        <img class="logo" src="./assets/logo.svg" style="float: left;" height="45px">
      </a>

      <a-dropdown :placement="placement">
        <div class="user-icon">
          <a-avatar style="vertical-align: middle;" :src="avatarUrl.replace(/http:/,'https:')" />
        </div>

        <a-menu slot="overlay">
          <a-menu-item disabled>{{userID}}</a-menu-item>
          <a-menu-item>
            <a href="/logout?state=">登出</a>
          </a-menu-item>
        </a-menu>
      </a-dropdown>

      <a-menu id="redir-header" theme="light" mode="horizontal" :style="{ lineHeight: '64px' }">
        <a-menu-item key="website">
          <a href="https://hustunique.com" target="_blank">团队官网</a>
        </a-menu-item>
        <a-menu-item key="bbs">
          <a href="https://bbs.hustunique.com" target="_blank">BBS</a>
        </a-menu-item>
      </a-menu>

    </a-layout-header>
    <a-layout style="min-height:100%;">
      <a-layout-sider breakpoint="lg" collapsedWidth="0" @collapse="onCollapse" style="padding:32px 0 0 0; background: #fff">
        <a-menu @select="select" theme="light" mode="inline" :defaultOpenKeys="['message']" :defaultSelectedKeys="['links']" :style="{ height: '100%', borderRight: 0 }" forceSubMenuRender>
          <a-menu-item key="/" :to="{ name: 'index' }">
            <a-icon type="appstore-o" />
            <span class="nav-text">网站索引</span>
          </a-menu-item>
          <a-sub-menu key="message">
            <span slot="title">
              <a-icon type="message" />消息推送</span>
            <a-menu-item key="/civil-sms" :to="{ name: 'civilSMS' }">
              <a-icon type="login" />内部短信
            </a-menu-item>
            <a-menu-item key="/outer-sms">
              <a-icon type="logout" />外部短信</a-menu-item>
            <a-menu-item key="/email-push">
              <a-icon type="mail" />邮件通知</a-menu-item>
            <a-menu-item key="/sms-reply">
              <a-icon type="rollback" />短信回复</a-menu-item>
          </a-sub-menu>

        </a-menu>
      </a-layout-sider>
      <a-layout-content :style="{ margin: '3% 5% 3% 5%' }">
        <div :style="{ padding: '4%', background: '#fff', minHeight: '360px' }">
          <router-view id="router" />
        </div>
      </a-layout-content>
    </a-layout>
    <a-layout>
    </a-layout>
  </a-layout>
</template>

<script>
const axios = require('axios')
const service = axios.create({
  baseURL: process.env.API_HOST,
  timeout: 5000,
  withCredentials: true
})
export default {
  methods: {
    onCollapse (collapsed, type) {
      console.log(collapsed, type)
    },
    select ({ item, key, selectedKeys }) {
      this.$router.push(key)
    }
  },
  data () {
    return {
      placement: 'bottomRight',
      userID: '',
      permission: [ ],
      avatarUrl: '/'
    }
  },
  mounted: function () {
    service
      .get('/open/permission', { timeout: 4000 })
      .then(function (response) {
        this.permission = response.data.data.permissions
        this.avatarUrl = response.data.data.avatar
        this.userID = response.data.data.userID
        console.log()
      }.bind(this))
      .catch(function (error) {
        if (error.response !== undefined) {
          this.$message.error(error.response.data.msg)
        } else {
          this.$message.error('电脑网络错误')
        }
      }.bind(this)
      )
  }
}
</script>

<style>
.logo {
  width: 160px;
  height: 48px;
  margin: 8px 30px 8px 10px;
  float: left;
}
.header {
  box-shadow: 0 2px 8px #f0f1f2;
  z-index: 100;
}
.user-icon {
  vertical-align: middle;
  margin: 0px 60px;
  float: right;
}
.ant-avatar:hover {
  box-shadow: 0px 1px 5px rgba(0, 72, 90, 0.3);
  transition: box-shadow 0.5s ease-in-out;
}
</style>



